import MeetupItem from '../MeetupItem';


import seoul from './seoul.jpg';
import hongkong from './hongkong.jpg';
import tokyo from './tokyo.jpg';

function AsiaMeetup() {
	return(
	<div className='container'>
		<MeetupItem image={seoul} link='/seoul' city='Seoul'/>
		<MeetupItem image={hongkong} link='/hongkong' city='Hongkong'/>
		<MeetupItem image={tokyo} link='/tokyo' city='Tokyo'/>
		<MeetupItem image={seoul} link='/seoul' city='Seoul'/>
		<MeetupItem image={hongkong} link='/hongkong' city='Hongkong'/>
		<MeetupItem image={tokyo} link='/tokyo' city='Tokyo'/>
		

	</div>
	)
}

export default AsiaMeetup;