import {useContext} from 'react';
import {Link} from 'react-router-dom';
import FavoritesContext from '../../store/favorites-context';

function Nav (){

	const favoritesCtx = useContext(FavoritesContext);
	return(
		<div >
		 <nav >
		  <ul>
		  	<li><Link className='navLink' to='/'>All Meetup</Link></li>
		  	<li><Link className='navLink' to='/europe'>Europe</Link></li>
		  	<li><Link className='navLink' to='/asia'>Asia</Link></li>
		  	<li><Link className='navLink' to='/america'>America</Link></li>
		  	<li><Link className='navLink' to='/favorite'>Favorites</Link></li>
		  	<li><Link className='navLink' to='/signin'>SignIn</Link></li>
		  	<li><Link className='navLink' to='/add'>Add Meetup</Link></li>
		  </ul>
		 </nav>
			
		</div>
		)
}

export default Nav;