import MeetupItem from '../MeetupItem';


import paris from './paris.jpg';
import frankfurt from './frankfurt.jpg';
import bcn from './bcn.jpg';

function EuropeMeetup() {
	return(
	<div className='container'>
		<MeetupItem image={paris} link='/paris' city='Paris'/>
		<MeetupItem image={frankfurt} link='/frankfurt' city='Frankfurt'/>
		<MeetupItem image={bcn} link='/barcelona' city='Barcelona'/>
		<MeetupItem image={paris} link='/paris' city='Paris'/>
		<MeetupItem image={frankfurt} link='/frankfurt' city='Frankfurt'/>
		<MeetupItem image={bcn} link='/barcelona' city='Barcelona'/>
		

	</div>
	)
}

export default EuropeMeetup;