import MeetupItem from '../MeetupItem';


import atlanta from './atlanta.jpg';
import seattle from './seattle.jpg';
import newyork from './newyork.jpg';

function AmericaMeetup() {
	return(
	<div className='container'>
		<MeetupItem image={atlanta} link='/atlanta' city='Atlanta'/>
		<MeetupItem image={seattle} link='/seattle' city='Seattle'/>
		<MeetupItem image={newyork} link='/newyork' city='Newyork'/>
		<MeetupItem image={atlanta} link='/atlanta' city='Atlanta'/>
		<MeetupItem image={seattle} link='/seattle' city='Seattle'/>
		<MeetupItem image={newyork} link='/newyork' city='Newyork'/>
		

	</div>
	)
}

export default AmericaMeetup;